import fy from './FdpYear.module.css'
import FdpMountRoute from "./FdpMount/FdpMountRoute";
import FullYear from "./FullYear";
import {Redirect, Route, Switch} from "react-router-dom";
import React from "react";

const FdpYear = (props) => {

    let initialState = {
        calendar: {
            mounts: [
                {idMount: 1, mount: '', mountNumber: '01'},
                {idMount: 2, mount: '', mountNumber: '02'},
                {idMount: 3, mount: '', mountNumber: '03'},
                {idMount: 4, mount: '', mountNumber: '04'},
                {idMount: 5, mount: '', mountNumber: '05'},
                {idMount: 6, mount: '', mountNumber: '06'},
                {idMount: 7, mount: '', mountNumber: '07'},
                {idMount: 8, mount: '', mountNumber: '08'},
                {idMount: 9, mount: '', mountNumber: '09'},
                {idMount: 10, mount: '', mountNumber: '10'},
                {idMount: 11, mount: '', mountNumber: '11'},
                {idMount: 12, mount: '', mountNumber: '12'},
            ],

            years: [
                {yearId: 2021},
                {yearId: 2022},
                {yearId: 2023},
            ],
        },
    }

    return <div className={fy.fdp}>
        <div>
            <Switch>
                <Route exact path='/fdp/summary' render={() => <FullYear
                    state={initialState}
                />}/>
                <Route path='/fdp/summary/:yearId' render={() => <FdpMountRoute
                    state={initialState}
                />}/>
                {/*       */}
                <Redirect to="/"/>
            </Switch>
        </div>
    </div>
}

export default FdpYear;