import React from 'react';
import ft from "./Table.module.css";

const TableWeightDifferences = (props) => {

    return (
        <table className={ft.table}>
            <thead>
            <tr>
                <th colSpan='6' className={ft.hsubtable}>    </th>
            </tr>
            <tr>
                <th colSpan='2'>  </th>
                <th>%   </th>
                <th> , </th>
                <th> </th>
                <th>       </th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td className={ft.three}></td>
                <td className={ft.four}>1000</td>
                <td className={ft.five}>10%</td>
                <td className={ft.six}></td>
                <td className={ft.seven}> </td>
                <td className={ft.eight}></td>
            </tr>
            </tbody>
        </table>
    )
}

export default TableWeightDifferences;