import logoLink from "../../images/khv_logo_fdp_link.png";
import p from "../../images/khv_logo_performance_link.png"
import pack from "../../images/khv_log_puck_link.png"
import nav from './Navbar.module.css'
import {NavLink} from "react-router-dom";
import {useSelector} from "react-redux";

const Navbar = () => {

    const isAuth = useSelector(state => state.user.isAuth)

    return (
        <div className={nav.navbar}>
            <ul className={nav.topmenu}>
                <li className={nav.link}>
                    <NavLink to="/homepage" activeClassName={nav.activeLink}></NavLink>
                </li>
                <li>
                    <NavLink to="/performance" activeClassName={nav.activeLink}><img src={p}/></NavLink>
                </li>
                <li className={nav.dropdown}>
                    <p className={nav.dropp}><img src={logoLink}/></p>
                    <ul className={nav.submenu}>
                        <NavLink to="/fdp/summary">  </NavLink>
                        {isAuth &&
                        <NavLink to="/fdp/listsCertificates"> </NavLink>
                        }
                        <NavLink to="/fdp/losses"></NavLink>
                        {/*<NavLink to="/fdp/dataTable"> </NavLink>*/}
                    </ul>
                </li>
                <li>
                    <p className={nav.dropp}><img src={pack}/></p>
                    {/*<NavLink to="/packing" activeClassName={nav.activeLink}></NavLink>*/}
                    <ul className={nav.submenu}>
                        <NavLink to=""></NavLink>
                        <NavLink to="">()</NavLink>
                        <NavLink to=""></NavLink>
                        <NavLink to=""></NavLink>
                        <NavLink to=""></NavLink>
                        <NavLink to=""></NavLink>
                    </ul>
                </li>
                <li className={nav.link}>
                    <NavLink to="/library" activeClassName={nav.activeLink}></NavLink>
                </li>
                {isAuth &&
                <li className={nav.link}>
                    <NavLink to="/reservation" activeClassName={nav.activeLink}></NavLink>
                </li>
                }
                {isAuth &&
                <li className={nav.link}>
                    <NavLink to="/promo" activeClassName={nav.activeLink}></NavLink>
                </li>
                }
                {isAuth &&
                <li className={nav.link}>
                    <NavLink to="/mernCloud" activeClassName={nav.activeLink}>""</NavLink>
                </li>
                }
            </ul>
        </div>
    )
}

export default Navbar;