import React from 'react';
import {NavLink} from 'react-router-dom';
import pd from './PerformanceDepartment.module.css'
import {useSelector} from "react-redux";

const PerformDepartment = () => {

    const role = useSelector(state => state.user.userRole)

    return (
        <div className={pd.link}>
            <div>
                <NavLink to={`/performance/all`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/so`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/ou`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/odp`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/osfif`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/oto`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/okp`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/ohp`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/ops`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/opf`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/uso`}></NavLink>
            </div>
            <div>
                <NavLink to={`/performance/worker`}></NavLink>
            </div>
            {role == 'admin' &&
            <button> </button>
            }
        </div>
    )
}

export default PerformDepartment;